import { Component } from "react";
import { Navigate } from "react-router-dom";
import { Formik, Field, Form, ErrorMessage } from "formik";
import * as Yup from "yup";
import { t } from "i18next";
import AuthService from "../services/auth.service";

type Props = {};

type State = {
    redirect: string | null,
    username: string,
    password: string,
    loading: boolean,
    message: string
};

export default class Login extends Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.handleLogin = this.handleLogin.bind(this);

        this.state = {
            redirect: null,
            username: "",
            password: "",
            loading: false,
            message: ""
        };
    }

    componentDidMount() {
        const currentUser = AuthService.getCurrentUser();

        if (currentUser) {
            this.setState({ redirect: "/profile" });
        }
    }

    componentWillUnmount() {
        window.location.reload();
    }

    validationSchema() {
        return Yup.object().shape({
            username: Yup.string().required(t('form.username.required')),
            password: Yup.string().required(t('form.password.required')),
        });
    }

    handleLogin(formValue: { username: string; password: string }) {
        const { username, password } = formValue;

        this.setState({
            message: "",
            loading: true
        });


        AuthService.login(username, password).then(
            () => {
                this.setState({
                    redirect: "/profile"
                });
            },
            error => {
                const errorMessage =
                    (error.response &&
                        error.response.data &&
                        error.response.data.message) ||
                    error.message ||
                    error.toString();

                // Use i18n to get the translated error message
                const translatedErrorMessage = t(`message.${errorMessage}`, {
                    defaultValue: errorMessage,
                });

                this.setState({
                    loading: false,
                    message: translatedErrorMessage
                });
            });
    }

    render() {
        if (this.state.redirect) {
            return <Navigate to={this.state.redirect} />
        }

        const { loading, message } = this.state;

        const initialValues = {
            username: "",
            password: "",
        };

        return (
            <div className="col-md-12">
                <div className="card card-container">
                    <img
                        src="//ssl.gstatic.com/accounts/ui/avatar_2x.png"
                        alt="profile-img"
                        className="profile-img-card"
                    />

                    <Formik
                        initialValues={initialValues}
                        validationSchema={this.validationSchema}
                        onSubmit={this.handleLogin}
                    >
                        <Form>
                            <div className="form-group">
                                <Field name="username" type="text" className="form-control" placeholder={t('form.username')} id="username" />
                                <ErrorMessage
                                    name="username"
                                    component="div"
                                    className="alert alert-danger"
                                />
                            </div>

                            <div className="form-group" style={{ paddingTop: '20px' }}>
                                <Field name="password" type="password" className="form-control" placeholder={t('form.password')} id="password" />
                                <ErrorMessage name="password" component="div" className="alert alert-danger" />
                            </div>

                            <div className="form-group" style={{ textAlign: 'center', paddingTop: '20px' }}>
                                <button type="submit" className="btn btn-primary" disabled={loading}>
                                    {loading && (
                                        <span className="spinner-border spinner-border-sm"></span>
                                    )}
                                    <span>{t('login.login')}</span>
                                </button>
                            </div>

                            {message && (
                                <div className="form-group">
                                    <div className="alert alert-danger" role="alert">
                                        {t('message.invalid_credentials')}
                                    </div>
                                </div>
                            )}
                        </Form>
                    </Formik>
                </div>
            </div>
        );
    }
}